<template>
    <div class="">
        <ul class="list-group">
            <div v-for="(topic, topic_index) in topics" :key="topic_index">
                <li
                    class="ps-2 list-group-item d-flex justify-content-between align-items-center text-primary"
                    @click="handleTopicExpand(topic_index)"
                >
                    {{ topic.label }}

                    <span class="btn btn-light"
                        ><i
                            :class="`fa-solid ${
                                topic_index === indexes.topic
                                    ? 'fa-chevron-up'
                                    : 'fa-chevron-down'
                            }`"
                        ></i
                    ></span>
                </li>
                <ul
                    class="ms-4 list-group"
                    v-if="topic_index === indexes.topic"
                >
                    <div
                        v-for="(level, level_index) in topic.value"
                        :key="level_index"
                    >
                        <li
                            class="ps-1 list-group-item d-flex justify-content-between align-items-center text-info"
                            @click="handleLevelExpand(level_index)"
                        >
                            {{ level.label }}
                            <span class="btn btn-light"
                                ><i
                                    :class="`fa-solid ${
                                        level_index === indexes.level
                                            ? 'fa-chevron-up'
                                            : 'fa-chevron-down'
                                    }`"
                                ></i
                            ></span>
                        </li>
                        <ul
                            class="ms-4 list-group"
                            v-if="level_index === indexes.level"
                        >
                            <li
                                :class="`ps-1 list-group-item d-flex justify-content-between align-items-center
                                ${
                                    isLevelSelected(
                                        {
                                            topic: topic_index,
                                            level: level_index,
                                            sub_level: sub_level_index,
                                        },
                                        selected_level
                                    )
                                        ? 'border border-primary text-primary'
                                        : ''
                                }
                                `"
                                v-for="(
                                    sub_level, sub_level_index
                                ) in level.value"
                                :key="sub_level_index"
                                @click="
                                    setLevel({
                                        topic: topic_index,
                                        level: level_index,
                                        sub_level: sub_level_index,
                                    })
                                "
                            >
                                {{ sub_level.label }}
                                <span
                                    class="text-primary"
                                    v-if="
                                        isLevelSelected(
                                            {
                                                topic: topic_index,
                                                level: level_index,
                                                sub_level: sub_level_index,
                                            },
                                            selected_level
                                        )
                                    "
                                    ><i class="fa-solid fa-circle-check"></i
                                ></span>
                            </li>
                        </ul>
                    </div>
                </ul>
            </div>
        </ul>
    </div>
</template>

<script>
import LocalStorage from "@/utils/LocalStorage.js";
import VocabulariesTopics from "@/data/japanese/index.js";

export default {
    data() {
        return {
            topics: null,
            items_list: null,
            default_level: null,
            selected_level: null,
            indexes: {
                topic: null,
                level: null,
                sub_level: null,
            },
        };
    },
    created() {
        this.init();
    },

    methods: {
        init() {
            this.localStorage = new LocalStorage();
            this.localStorage.setLanguageKey("japanese-vietnamese");
            this.default_level = this.localStorage.default_level;
            /* eslint-disable */
            switch (this.localStorage.language_key) {
                case "japanese-english":
                    this.topics = VocabulariesTopics.english;
                    break;
                case "japanese-vietnamese":
                    this.topics = VocabulariesTopics.vietnamese;
                    break;
                default:
                    this.topics = VocabulariesTopics.vietnamese;
                    break;
            }
            this.selected_level = this.localStorage.setting.level;
            this.items_list =
                this.topics[this.selected_level.topic].value[
                    this.selected_level.level
                ].value[this.selected_level.sub_level].value;

            this.indexes = { ...this.selected_level };
        },
        setLevel(selected_level = this.default_level) {
            this.selected_level = selected_level;
            this.localStorage.setLevel(selected_level);
        },
        isLevelSelected(level = null, selected_level = null) {
            if (selected_level === null || level === null) return false;
            return (
                selected_level.topic === level.topic &&
                selected_level.level === level.level &&
                selected_level.sub_level === level.sub_level
            );
        },
        handleTopicExpand(topic_index) {
            this.indexes.topic = !this.indexes.topic
                ? topic_index
                : this.indexes.topic === topic_index
                ? null
                : topic_index;
        },
        handleLevelExpand(level_index) {
            this.indexes.level = !this.indexes.level
                ? level_index
                : this.indexes.level === level_index
                ? null
                : level_index;

            console.log({ indexes_level: this.indexes.level });
        },
    },
};
</script>
